.page {
  font-family: "Montserrat", sans-serif; }

.page-header {
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  height: 50px; }
  .page-header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 83.3vw; }
    @media (min-width: 768px) {
      .page-header__inner {
        width: 94.4vw; } }
  .page-header__icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60px; }
  .page-header__basket {
    cursor: pointer; }
  .page-header__burger {
    cursor: pointer; }
    @media (min-width: 768px) {
      .page-header__burger {
        display: none; } }
  .page-header__phone {
    display: none;
    cursor: pointer; }
    @media (min-width: 768px) {
      .page-header__phone {
        display: block; } }
  .page-header__nav {
    display: none; }
    @media (min-width: 768px) {
      .page-header__nav {
        display: flex; } }

.work-time__header {
  display: none; }
  @media (min-width: 768px) {
    .work-time__header {
      display: block; } }

.main-nav__toggle {
  display: none; }

.main-nav__list {
  display: flex; }

.book-card {
  display: flex;
  height: 490px;
  width: 260px; }
  .book-card__link {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between; }
  .book-card__title {
    order: 2;
    font-size: 24px;
    color: #000000; }
  .book-card__img {
    order: 1;
    width: 220px;
    height: 310px; }
  .book-card__descr {
    order: 3;
    font-size: 16px;
    font-style: italic;
    color: #000000; }
  .book-card__price {
    order: 4;
    justify-content: flex-end;
    font-size: 16px;
    font-weight: 800;
    color: #000000; }

.promo {
  background-color: #f5f5f5;
  padding-bottom: 40px; }
  .promo__inner {
    width: 83%;
    margin: 0 auto; }

.catalog {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .catalog__inner {
    max-width: 1160px; }
  .catalog__title {
    display: none; }
  .catalog__content {
    display: flex; }
  .catalog__cards {
    display: flex;
    flex-direction: column;
    align-items: center; }
    @media (min-width: 768px) {
      .catalog__cards {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center; } }
  .catalog__pagination {
    display: flex;
    justify-content: space-around; }

.slider {
  position: relative; }
  .slider__item {
    display: flex; }
  .slider__subtitle {
    font-style: italic;
    font-size: 14px; }
  .slider__descr-wrap {
    border: 8px solid #e8e8e8;
    padding: 30px 10px; }
  .slider__title {
    font-size: 24px; }
  .slider__item-more {
    font-size: 16px;
    color: #292df7;
    font-weight: 400; }
  .slider__item-link {
    display: none; }
    @media (min-width: 1200px) {
      .slider__item-link {
        display: block; } }
  .slider__item-img {
    width: 300px;
    height: 425px; }

.new__catalog {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  overflow-x: auto; }

.page-footer__inner {
  display: flex;
  justify-content: space-between; }

.page-footer__nav {
  display: none; }
  @media (min-width: 768px) {
    .page-footer__nav {
      display: flex; } }

.tabs {
  width: 100%;
  height: 50px;
  padding-bottom: 40px; }
  .tabs__inner {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    background-color: #e8e8e8;
    height: 50px; }
  .tabs__item {
    margin-left: 30px;
    padding-top: 25px; }
  .tabs__link:after {
    content: '';
    height: 3px;
    width: 20px;
    background-color: #ef529f; }
  .tabs__link--active {
    border-bottom: 3px solid #ef529f; }

.delivery__inner {
  position: relative;
  box-sizing: border-box;
  box-shadow: inset 0 0 0 8px #e8e8e8;
  padding: 30px 30px;
  width: 94.4vw;
  margin: 0 auto; }
  .delivery__inner:before {
    content: '';
    position: absolute;
    top: -1px;
    left: 60px;
    width: 44.1%;
    height: 10px;
    background-color: #fff; }
    @media (min-width: 768px) {
      .delivery__inner:before {
        width: 22.3%; } }
    @media (min-width: 1200px) {
      .delivery__inner:before {
        width: 32.78%; } }
  .delivery__inner:after {
    content: '';
    position: absolute;
    bottom: -1px;
    right: 60px;
    width: 32.3%;
    height: 10px;
    background-color: #fff; }
    @media (min-width: 768px) {
      .delivery__inner:after {
        width: 36.8%; } }
    @media (min-width: 1200px) {
      .delivery__inner:after {
        width: 60.65%; } }

.delivery__list {
  display: flex;
  flex-direction: column;
  align-items: center; }

.delivery__item {
  display: flex;
  flex-direction: column;
  align-items: center; }

.custom-arrow {
  margin-top: 30px; }
